import React, { useState } from 'react';
import copy from "copy-to-clipboard";  
function Textarea(props) {
    const [text, setText] = useState('');
    const Change=(event) =>{
        // console.log("changed");
        setText(event.target.value);
    };
    const ClickUpper=() =>{
        // console.log(text);
        let newText=text.toUpperCase();
        setText(newText);
        props.showAlert("changed to uppercase","success")
    };
    const ClickLower=()=>{
        let newText=text.toLowerCase();
        setText(newText);
        props.showAlert("changed to lowercase","success")
    }
    const ClickClear=()=>{
        setText("");
        props.showAlert("text cleared","success")
    }
    // setText('alsdkjf');
    function removeFromSplitArray(element){
        if(element!==""){
            return true;
        }
    }
    // const copyToClipboard = (e) => {
    //     e.target.value.select();
    //     document.execCommand('copy');
    //     // This is just personal preference.
    //     // I prefer to not show the whole text area selected.
    //     e.target.focus();
    //     // this.setState({ copySuccess: 'Copied!' });
    //   };
    const copyToClipboard = () => {
        copy(text);
        // alert(`You have copied "${text}"`);
        props.showAlert("text copied","success");
    }
    return (
        <>
            <div className="mb-3 container">
                <h1 className="mb-3" style={{color: props.Mode==="light"?"black":"white"}}>Input text </h1>
                <textarea  placeholder='Enter something' className="form-control mb-3" value={text} onChange={Change} id="exampleFormControlTextarea1" rows="7"></textarea>
                <button disabled={text.length===0} className="btn btn-primary mx-2 my-1" onClick={ClickUpper}>click to Uppercase</button>
                <button disabled={text.length===0} className="btn btn-primary mx-2 my-1" onClick={ClickLower}>click to Lowercase</button>
                <button disabled={text.length===0} className="btn btn-primary mx-2 my-1" onClick={copyToClipboard}>copy text</button>
                <button disabled={text.length===0} className="btn btn-danger mx-2 my-1" onClick={ClickClear}>clear</button>
            </div>
            <div className="container" style={{color: props.Mode==="light"?"black":"white"}}>
                <p>total words : {text.split(" ").filter(removeFromSplitArray).length} total characters : {text.length}</p>
                <h1>Summary</h1>
                <p>{text===""?"Enter something in above textbox for summary":text}</p>
            </div>
        </>
    );
}
export default Textarea;